@import './Variables.scss';
@import './Responsive.scss';


.login_page{
    display: flex;
    width: 100%;
    height: 100%;

    .page_left{
        flex: 3;
        position: relative;
        background-image: url('../Images/images.jpeg');
        background-size: cover;
        background-repeat: repeat;
        
        .over_design{
            position: absolute;
            display: flex;
            width: 100%;
            height: 100%;
            background: rgb(100 24 105 / 54%);
            top: 0;
            left: 0;
            right: 0;


            .header-text{
                font-size: 3rem;
                padding: 10px;
                padding-top: 3rem;
                color: $sec_color;
                width: 80%;
                line-height: 0.99;
            }
        }
    }

    .page_right{
        background: $sec_color;
        display: flex;
        justify-content: center;
        flex: 2;

        .wrapper{
            width: 100%;
            display: flex;
            flex-direction: column;
            text-transform: capitalize;
            margin-top: 3rem;
            padding: 10px;

            .text-top{
                padding: 5px;
                font-size: 1.3rem;
                padding-left: 1.3rem;
                color: orange;
            } .error{
                    margin: 0;
                    margin-left: 2rem;
                    color: red;
                    text-transform: capitalize;
                    font-size: .8rem;
                }
            .MuiFormGroup-root{
                display: flex;
                flex-direction: column;
                margin: 0 auto;
                padding-top: 1rem;;
                gap: 1rem;
                width: 90%;

               .MuiTextField-root{
                display: flex;
                width: 100%;
                margin: 0 auto;
                    input{
                         &.placeholder{  
                    color: red;
                }

                    }
             
               }

                .btn{
                    width: 100%;
                    display: flex;
                    justify-content: flex-end;
                    .MuiButtonBase-root{
                    width: 175px;
                    padding: 10px;
                }  
                }
           
            }

            .p_text{
                display: flex;
                margin-top: 3rem;
                width: 100%;
                padding-left: 1.5rem;

                p{
                    margin: 0;
                    padding: 0;
                    color:grey;
                    text-align: left;

                    a{
                        color: purple;
                        font-weight: 500;
                        padding: 5px;
                    }
                }
            }
        }
    }

    @include small{
        .page_right{

            .wrapper{
                margin: 1rem;
            }
        }
    }
    
    @include medium{
        .page_left{
            display: none;
            
        }

        .page_right{
            flex: 1;
            background-image: url('../Images/images.jpeg');
            background-size: cover;

            .wrapper{
                background-color: rgb(255 234 253 / 34%);
                justify-content: center;
                padding: 0;
                overflow: hidden;
                margin: 2rem auto;
                margin-right: 5rem;
                margin-left: 5rem;
                
                @include small{
                    justify-content: flex-start;
                    background-color: rgb(255 234 253 / 34%);
                    width: 100%;
                    height: 100%;
                    margin: 0;
                    padding-top: 5em;
                }
            }
        }
    }
}